import React, { useCallback } from 'react';
import app from '../../base';

export default function ExpertSession({ history }) {
  const logOut = useCallback(async () => {
    try {
      await app.auth().signOut();
      history.push('/');
    } catch (error) {
      alert(error.message);
    }
  }, [history]);

  return (
    <div>
      <button onClick={logOut}>Sign Out</button>
    </div>
  );
}
